
import { computed, defineComponent, reactive, ref } from "vue";
import { loader } from "@/utils/loader";
import router from "@/router";
import BaseReportTable from "@/apps/EMC/Components/tables/BaseReportTable.vue";
import { CustomFilterInterface, RowActionButtonInterface, TableColumnInterface } from "@uniquedj95/vtable";
import { CtIndicator, DataCleaningReportService } from "@/apps/ART/services/reports/data_cleaning_report_service";
import { DISPLAY_DATE_FORMAT } from "@/utils/Date";
import dayjs from "dayjs";
import { toGenderString } from "@/utils/Strs";
import { sortByARV } from "../../utils/common";
import { Option } from "@/components/Forms/FieldInterface";

export default defineComponent({
  name: "DataCleaning",
  components: { BaseReportTable },
  setup() {
    const rows = ref<any[]>([]);
    const tool = reactive({} as Option);
    const title = computed(() => `Data Cleaning Tools ${ tool?.value || "" }`);
    const period = ref("-");
    
    const columns: TableColumnInterface[] = [
      { path: "arv_number", label: "ARV Number", preSort: sortByARV, initialSort: true },
      { path: "given_name", "label": "First Name" },
      { path: "family_name", label: "Last Name" },
      { path: "gender", label: "Gender", formatter: toGenderString },
      { path: "birthdate", label: "Date of Birth", formatter: (v) => dayjs(v).format(DISPLAY_DATE_FORMAT) },
    ]

    const fetchData = async (filters: Record<string, any>) => {
      await loader.show();
      const report = new DataCleaningReportService()
      report.setStartDate(filters.dateRange.startDate)
      report.setEndDate(filters.dateRange.endDate)
      Object.assign(tool, filters.tool)
      period.value = report.getDateIntervalPeriod()
      rows.value = await report.getCleaningToolReport(tool.value as CtIndicator)
      await loader.hide();
    }  

    const rowActionBtns: RowActionButtonInterface[] = [{ 
      label: "Show", 
      default: true, 
      action: (row) => {
        router.push(`/emc/patient/${row['patient_id']}`)
      } 
    }]

    const filters = computed<CustomFilterInterface[]>(() => [
      {
        id: "tool",
        label: "Select Cleaning Tool",
        type: "select",
        value: tool,
        gridSize: 5,
        options: Object.values(CtIndicator).map(indicator => {
          return { label: indicator.toLowerCase(), value: indicator }
        }),
      }
    ])

    return {
      period,
      title,
      rows,
      columns,
      rowActionBtns,
      filters,
      fetchData,
    }
  }
})
